<template>
	<div class="password">
		<!--  忘记密码-->
		<div class="denglu">
			<div class="denglu-dl">忘记密码</div>
			<!--输入手机号，验证码，密码-->
			<div class="denglu-inp">
				<div class="inp inp1">
					<img src="/img/login-shoujihao.png" alt="">
					<input type="text" name="user" placeholder="手机号" v-model="username">
				</div>
				<div class="inp inp2">
					<img src="/img/login-yanzhengma.png" alt="">
					<input type="text" name="yzm" placeholder="验证码" v-model="yanzhengma">
					<button class="btn-default btn">获取验证码</button>
				</div>
				<div class="inp inp3">
					<img src="/img/login-mima.png" alt="">
					<input type="password" name="password" placeholder="密码" v-model="pwd">
					<div class="img">
						<img src="/img/login-yanjing_bi.png" alt="">
					</div>
				</div>
			</div>
			<!--登录按钮-->
			<div class="denglu-btn">
				<button class="btn-default btn" @click="submit">完成</button>
			</div>
		</div>
		<!--  快速登录-->
		<div class="bj">
			<div class="top">快速登录</div>
			<div class="bottom">
				<ul>
					<li v-for="(item, index) in loginLogo" :key="index">
						<img :src="item" alt="">
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'password',
		data: function() {
			return {
				loginLogo: [
					'/img/weixin.png'
				],
				show: false,
				username: null,
				yanzhengma: 1234,
				pwd: '',
				len:11
			}
		},
		methods: {
			// 提交
			submit() {
				var regx = /[a-zA-Z0-9]{8,16}/
				// 判断手机号，验证码，密码输入格式是否正确
				// eslint-disable-next-line eqeqeq
				if(this.username.length == this.len) {
					// eslint-disable-next-line eqeqeq
					if(this.yanzhengma == 1234) {
						if(regx.test(this.password)) {
							// 发送ajax请求数据
							this.$axios.get('/json/users.json').then(res => {
								//									console.log(res.data)
								for(var i in res.data) {
									// eslint-disable-next-line eqeqeq
									if(this.username == res.data[i].username) {
										alert('用户名已经被占用')
										break
									} else {
										// eslint-disable-next-line eqeqeq
										if(i == res.data.length - 1) {
											console.log('更改用户信息到数据库')
											alert('密码找回成功')
											// 找回密码成功跳转到登陆页面
											this.$router.push('/login')
										} else {
											continue
										}
									}
								}
							}).catch(err => {
								console.log(err)
							})
						} else {
							console.log('密码格式错误，必须包涵数字、字母，并且在8-16位')
						}
					} else {
						console.log('验证码错误')
					}
				} else {
					console.log('用户名格式错误')
				}
			}
		}
	}
</script>

<style scoped>
	a {
		text-decoration: none;
