	}
	
	.password .denglu-dl {
		width: 100%;
		height: 100px;
		font-size: 30px;
		line-height: 100px;
		text-align: center;
		margin: 60px 0 0 0;
	}
	/*  输入手机号，密码*/
	
	.password .denglu-inp {
		width: 580px;
		margin: 100px auto 0;
	}
	
	.password .denglu-inp .inp1 {
		margin-bottom: 60px;
	}
	
	.password .denglu-inp .inp {
		margin-left: 10%;
